@use '../variables'
@use '../mixins' 
    
#information
    @include mixins.center
    flex-direction: column
    background-color: variables.$bg-color-secondary
    padding: 1rem
    margin-bottom: 2rem
    text-align: left
    width: 80%
    border-radius: variables.$card-border-radius

        

    .info-card
        @include mixins.center
        justify-content: start
        gap: 1rem
        // padding-bottom: .8rem
        // margin-bottom: .8rem
        border-bottom: 1px solid #444
        width: 100%
        // margin-left: 0
        // margin-right: 0

        div
            margin-left: 0
            margin-right: 0

        h3
            color: variables.$text-color-secondary
            font-size: .8rem
            margin-bottom: .5rem

        p
            color: variables.$text-color
            font-weight: bold
            margin-top: 0

        svg
            background-color: variables.$bg-color
            padding: 0.4rem
            width: 2rem
            height: 2rem
            border-radius: variables.$card-border-radius
            margin-left: 0
            margin-right: 0

        #phone-icon
            color: #e93b81

        #pin-icon
            color: #6ab5b9

        #email-icon
            color: #c17ceb